import { customElement, state } from 'lit/decorators.js';
import { ref, createRef } from 'lit/directives/ref.js';
import { html, TemplateResult } from 'lit';

import { Base } from '../base';
import type {
  ChartComponentLike,
  ChartData,
  ChartOptions,
  ChartType,
  Plugin,
} from 'chart.js';
import { Chart } from 'chart.js';
import { legendMargin } from '../../plugins';
import { deepMerge } from '../../utils';

/**
 * Base Chart of chartjs-web-components
 */
@customElement('zwc-base-chart')
export abstract class ZwcBaseChart extends Base {
  public abstract componentsToRegister: ChartComponentLike[];
  public abstract type: ChartType;
  public abstract options: ChartOptions;
  public abstract defaultOptions: ChartOptions;
  public abstract data: ChartData;

  public chartConstructor = Chart;
  public chart: Chart | undefined;
  public plugins: Plugin<ChartType>[] = [];

  canvasRef = createRef<HTMLCanvasElement>();
  defaultPlugins: Plugin<ChartType>[] = [legendMargin];

  @state()
  get mergedOptions(): ChartOptions {
    return deepMerge(this.defaultOptions, this.options);
  }

  public firstUpdated(): void {
    if (!this.chart) {
      const ctx: CanvasRenderingContext2D | null | undefined =
        this.canvasRef.value?.getContext('2d');

      if (ctx) {
        this.chartConstructor.register(...this.componentsToRegister);
        this.chart = new this.chartConstructor(ctx, {
          type: this.type,
          options: this.mergedOptions,
          data: this.data,
          plugins: [...this.defaultPlugins, ...this.plugins],
        });
      }
    }
  }

  // On init
  public connectedCallback(): void {
    super.connectedCallback();
    window.addEventListener('resize', this.handleResize.bind(this));
  }

  // On destroy
  public disconnectedCallback(): void {
    super.disconnectedCallback();
    window.removeEventListener('resize', this.handleResize);
  }

  protected handleResize(): void {
    this.chart?.resize();
  }

  public override render(): TemplateResult {
    return html`
      <style>
        .chart-size {
          position: relative;
        }
        canvas {
          width: 400px;
          height: 400px;
        }
      </style>
      <div class="chart-size">
        <canvas ${ref(this.canvasRef)}></canvas>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-base-chart': ZwcBaseChart;
  }
}
